import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useSpring, animated } from '@react-spring/web'
import { Github, AlertCircle, Key } from 'lucide-react'
import { api } from '@/lib/api'

export default function LoginDirect() {
  const navigate = useNavigate()
  const [error, setError] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)
  const [githubToken, setGithubToken] = useState('')

  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'scale(0.95)' },
    to: { opacity: 1, transform: 'scale(1)' },
    config: { tension: 200, friction: 25 }
  })

  const handleTokenLogin = async () => {
    if (!githubToken.trim()) {
      setError('Please enter a GitHub token')
      return
    }

    setLoading(true)
    setError(null)

    try {
      // For demo purposes, we'll validate the token directly with GitHub
      const response = await fetch('https://api.github.com/user', {
        headers: {
          'Authorization': `Bearer ${githubToken}`,
          'Accept': 'application/vnd.github.v3+json'
        }
      })

      if (!response.ok) {
        throw new Error('Invalid GitHub token')
      }

      const userData = await response.json()
      
      // Check if user is allowed
      const ALLOWED_USERS = ['aspenas', 'aaron', 'tyler']
      if (!ALLOWED_USERS.includes(userData.login)) {
        throw new Error(`User ${userData.login} is not authorized to access this dashboard`)
      }

      // Create a mock session token
      const sessionToken = btoa(JSON.stringify({
        user: userData.login,
        name: userData.name || userData.login,
        avatar: userData.avatar_url,
        expires: Date.now() + (24 * 60 * 60 * 1000)
      }))

      api.setToken(sessionToken)
      navigate('/')
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Authentication failed')
    } finally {
      setLoading(false)
    }
  }


  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center p-4">
      <animated.div style={containerSpring} className="w-full max-w-md">
        <div className="glass rounded-2xl p-8 space-y-6">
          <div className="text-center space-y-2">
            <h1 className="text-3xl font-bold gradient-text">Claude Review Dashboard</h1>
            <p className="text-gray-600 dark:text-gray-400">
              Sign in with your GitHub token
            </p>
          </div>

          {error && (
            <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
              <div className="flex items-start gap-3">
                <AlertCircle className="h-5 w-5 text-red-600 dark:text-red-400 flex-shrink-0 mt-0.5" />
                <div>
                  <h3 className="font-medium text-red-800 dark:text-red-200">
                    Authentication Error
                  </h3>
                  <p className="text-sm text-red-600 dark:text-red-400 mt-1">
                    {error}
                  </p>
                </div>
              </div>
            </div>
          )}

          <div className="space-y-4">
            <div className="space-y-2">
              <label htmlFor="github-token" className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                GitHub Personal Access Token
              </label>
              <input
                id="github-token"
                type="password"
                value={githubToken}
                onChange={(e) => setGithubToken(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleTokenLogin()}
                placeholder="ghp_..."
                className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
              <p className="text-xs text-gray-500 dark:text-gray-400">
                Your token needs 'read:user' scope
              </p>
            </div>

            <button
              onClick={handleTokenLogin}
              disabled={loading || !githubToken.trim()}
              className="w-full flex items-center justify-center gap-3 px-6 py-3 bg-gray-900 dark:bg-white text-white dark:text-gray-900 rounded-lg hover:bg-gray-800 dark:hover:bg-gray-100 transition-colors disabled:opacity-50"
            >
              <Key className="h-5 w-5" />
              {loading ? 'Authenticating...' : 'Sign in with Token'}
            </button>

            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300 dark:border-gray-600"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-gray-50 dark:bg-gray-900 text-gray-500">OR</span>
              </div>
            </div>

            <button
              onClick={() => window.location.href = 'https://github.com/settings/tokens/new?description=Claude%20Review%20Dashboard&scopes=read:user'}
              className="w-full flex items-center justify-center gap-3 px-6 py-3 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors"
            >
              <Github className="h-5 w-5" />
              Create GitHub Token
            </button>

            <p className="text-xs text-center text-gray-500 dark:text-gray-400">
              Only authorized users (Aaron, Tyler, and Patrick) can access this dashboard
            </p>
          </div>
        </div>

        <div className="text-center mt-6 space-y-2">
          <p className="text-sm text-gray-500 dark:text-gray-400">
            For quick access, visit:
          </p>
          <a 
            href="/test-login.html" 
            className="text-sm text-blue-600 dark:text-blue-400 hover:underline"
          >
            Test Login Page (with token)
          </a>
        </div>
      </animated.div>
    </div>
  )
}