import { useSpring, animated } from '@react-spring/web'
import { 
  AreaChart, 
  Area, 
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  ResponsiveContainer 
} from 'recharts'
import { formatCurrency } from '@/lib/utils'

interface MonthlyComparisonChartProps {
  data: Array<{
    date: string
    cost: number
    count: number
  }>
}

export default function MonthlyComparisonChart({ data }: MonthlyComparisonChartProps) {
  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'scale(0.95)' },
    to: { opacity: 1, transform: 'scale(1)' },
    delay: 500,
    config: { tension: 200, friction: 25 }
  })

  // Group data by week for better visualization
  const weeklyData = data.reduce((acc: any[], item, index) => {
    const weekIndex = Math.floor(index / 7)
    if (!acc[weekIndex]) {
      acc[weekIndex] = {
        week: `Week ${weekIndex + 1}`,
        cost: 0,
        count: 0,
        avgCost: 0
      }
    }
    acc[weekIndex].cost += item.cost
    acc[weekIndex].count += item.count
    acc[weekIndex].avgCost = acc[weekIndex].cost / acc[weekIndex].count
    return acc
  }, [])

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-black/80 text-white p-3 rounded-lg">
          <p className="font-medium">{label}</p>
          <p className="text-sm">Total: {formatCurrency(payload[0].value)}</p>
          <p className="text-sm">Reviews: {payload[0].payload.count}</p>
          <p className="text-sm">Avg: {formatCurrency(payload[0].payload.avgCost)}</p>
        </div>
      )
    }
    return null
  }

  return (
    <animated.div style={containerSpring} className="glass rounded-xl p-6">
      <div className="mb-4">
        <h3 className="text-lg font-semibold">Weekly Trends</h3>
        <p className="text-sm text-gray-600 dark:text-gray-400">Cost trends aggregated by week</p>
      </div>
      <div className="h-80">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart data={weeklyData}>
            <defs>
              <linearGradient id="colorCost" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#a855f7" stopOpacity={0.8}/>
                <stop offset="95%" stopColor="#a855f7" stopOpacity={0.1}/>
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" stroke="rgba(0, 0, 0, 0.05)" />
            <XAxis 
              dataKey="week" 
              tick={{ fontSize: 12 }}
              tickLine={false}
              axisLine={false}
            />
            <YAxis 
              tick={{ fontSize: 12 }}
              tickLine={false}
              axisLine={false}
              tickFormatter={(value) => `$${value}`}
            />
            <Tooltip content={<CustomTooltip />} />
            <Area
              type="monotone"
              dataKey="cost"
              stroke="#a855f7"
              strokeWidth={2}
              fillOpacity={1}
              fill="url(#colorCost)"
              animationDuration={1000}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </animated.div>
  )
}