import { useState } from 'react'
import { useQuery } from '@tanstack/react-query'
import { useSpring, animated, useTrail } from '@react-spring/web'
import { 
  Calendar,
  Download,
  TrendingUp,
  BarChart3,
  DollarSign,
  AlertTriangle
} from 'lucide-react'
import { format, subDays, startOfMonth } from 'date-fns'
import { api, mockData } from '@/lib/api'
import { formatCurrency, cn } from '@/lib/utils'
import CostByTypeChart from '@/components/charts/CostByTypeChart'
import MonthlyComparisonChart from '@/components/charts/MonthlyComparisonChart'
import CostProjections from '@/components/CostProjections'

export default function CostAnalysis() {
  const [dateRange, setDateRange] = useState<'7d' | '30d' | 'mtd' | 'custom'>('30d')
  const [customStartDate, setCustomStartDate] = useState('')
  const [customEndDate, setCustomEndDate] = useState('')

  const getDateRange = () => {
    const now = new Date()
    switch (dateRange) {
      case '7d':
        return {
          start: format(subDays(now, 7), 'yyyy-MM-dd'),
          end: format(now, 'yyyy-MM-dd')
        }
      case '30d':
        return {
          start: format(subDays(now, 30), 'yyyy-MM-dd'),
          end: format(now, 'yyyy-MM-dd')
        }
      case 'mtd':
        return {
          start: format(startOfMonth(now), 'yyyy-MM-dd'),
          end: format(now, 'yyyy-MM-dd')
        }
      case 'custom':
        return {
          start: customStartDate,
          end: customEndDate
        }
    }
  }

  const { start, end } = getDateRange()

  const { data: costSummary, isLoading } = useQuery({
    queryKey: ['costAnalysis', start, end],
    queryFn: async () => {
      if (import.meta.env.DEV) return mockData.costSummary
      return api.getCostSummary(start, end)
    }
  })

  const headerSpring = useSpring({
    from: { opacity: 0, transform: 'translateY(-20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    config: { tension: 200, friction: 25 }
  })

  const insights = [
    {
      title: 'Cost Efficiency',
      value: costSummary ? `${((costSummary.total_cost / costSummary.total_reviews) < 3.5 ? 'Good' : 'Review Needed')}` : '-',
      description: 'Average cost per review is ' + (costSummary ? (costSummary.average_cost_per_review < 3.5 ? 'below' : 'above') : '...') + ' target',
      icon: TrendingUp,
      color: costSummary && costSummary.average_cost_per_review < 3.5 ? 'text-cost-low' : 'text-cost-high'
    },
    {
      title: 'Top Spender',
      value: costSummary ? Object.entries(costSummary.cost_by_repository).sort(([,a], [,b]) => b - a)[0]?.[0] || '-' : '-',
      description: 'Repository with highest costs',
      icon: AlertTriangle,
      color: 'text-amber-600'
    },
    {
      title: 'Most Used Type',
      value: costSummary ? Object.entries(costSummary.cost_by_type).sort(([,a], [,b]) => b - a)[0]?.[0] || '-' : '-',
      description: 'Most frequent review type',
      icon: BarChart3,
      color: 'text-claude-600'
    }
  ]

  const trail = useTrail(insights.length, {
    from: { opacity: 0, transform: 'scale(0.9)' },
    to: { opacity: 1, transform: 'scale(1)' },
    config: { tension: 200, friction: 25 }
  })

  const handleExport = () => {
    if (!costSummary) return
    
    const csvContent = [
      ['Date', 'Cost', 'Reviews'],
      ...costSummary.cost_by_day.map(d => [d.date, d.cost.toFixed(2), d.count])
    ].map(row => row.join(',')).join('\n')
    
    const blob = new Blob([csvContent], { type: 'text/csv' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = `claude-review-costs-${start}-to-${end}.csv`
    a.click()
    URL.revokeObjectURL(url)
  }

  return (
    <div className="p-8 space-y-8">
      {/* Header */}
      <animated.div style={headerSpring} className="flex items-start justify-between">
        <div>
          <h1 className="text-3xl font-bold gradient-text">Cost Analysis</h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Deep dive into your Claude review costs and trends
          </p>
        </div>
        
        <button
          onClick={handleExport}
          className="flex items-center gap-2 px-4 py-2 bg-claude-500 text-white rounded-lg hover:bg-claude-600 transition-colors"
        >
          <Download className="h-4 w-4" />
          Export CSV
        </button>
      </animated.div>

      {/* Date Range Selector */}
      <animated.div 
        style={useSpring({
          from: { opacity: 0, transform: 'translateY(20px)' },
          to: { opacity: 1, transform: 'translateY(0px)' },
          delay: 100
        })}
        className="glass rounded-xl p-6"
      >
        <div className="flex items-center gap-3 mb-4">
          <Calendar className="h-5 w-5 text-claude-500" />
          <h3 className="text-lg font-semibold">Date Range</h3>
        </div>
        
        <div className="flex flex-wrap gap-3">
          {(['7d', '30d', 'mtd', 'custom'] as const).map((range) => (
            <button
              key={range}
              onClick={() => setDateRange(range)}
              className={cn(
                'px-4 py-2 rounded-lg font-medium transition-all',
                dateRange === range
                  ? 'bg-claude-500 text-white'
                  : 'bg-gray-100 dark:bg-gray-800 text-gray-700 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700'
              )}
            >
              {range === '7d' && 'Last 7 Days'}
              {range === '30d' && 'Last 30 Days'}
              {range === 'mtd' && 'Month to Date'}
              {range === 'custom' && 'Custom Range'}
            </button>
          ))}
        </div>
        
        {dateRange === 'custom' && (
          <div className="mt-4 flex gap-3">
            <input
              type="date"
              value={customStartDate}
              onChange={(e) => setCustomStartDate(e.target.value)}
              className="px-3 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg"
            />
            <span className="self-center text-gray-500">to</span>
            <input
              type="date"
              value={customEndDate}
              onChange={(e) => setCustomEndDate(e.target.value)}
              className="px-3 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg"
            />
          </div>
        )}
      </animated.div>

      {/* Key Insights */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {trail.map((style, index) => {
          const insight = insights[index]
          return (
            <animated.div
              key={insight.title}
              style={style}
              className="glass rounded-xl p-6"
            >
              <div className="flex items-start justify-between mb-3">
                <div className={`p-2 rounded-lg bg-gray-100 dark:bg-gray-800 ${insight.color}`}>
                  <insight.icon className="h-5 w-5" />
                </div>
              </div>
              <h4 className="text-sm font-medium text-gray-600 dark:text-gray-400">
                {insight.title}
              </h4>
              <p className="text-2xl font-bold mt-1">
                {isLoading ? (
                  <span className="skeleton inline-block w-24 h-8 rounded" />
                ) : (
                  insight.value
                )}
              </p>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-2">
                {insight.description}
              </p>
            </animated.div>
          )
        })}
      </div>

      {/* Cost Summary */}
      {costSummary && (
        <animated.div
          style={useSpring({
            from: { opacity: 0 },
            to: { opacity: 1 },
            delay: 300
          })}
          className="glass rounded-xl p-6"
        >
          <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
            <DollarSign className="h-5 w-5 text-claude-500" />
            Cost Summary
          </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4">
              <p className="text-sm text-gray-600 dark:text-gray-400">Total Cost</p>
              <p className="text-2xl font-bold mt-1">{formatCurrency(costSummary.total_cost)}</p>
            </div>
            <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4">
              <p className="text-sm text-gray-600 dark:text-gray-400">Total Reviews</p>
              <p className="text-2xl font-bold mt-1">{costSummary.total_reviews}</p>
            </div>
            <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4">
              <p className="text-sm text-gray-600 dark:text-gray-400">Input Tokens</p>
              <p className="text-2xl font-bold mt-1">{(costSummary.total_input_tokens / 1000000).toFixed(1)}M</p>
            </div>
            <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4">
              <p className="text-sm text-gray-600 dark:text-gray-400">Output Tokens</p>
              <p className="text-2xl font-bold mt-1">{(costSummary.total_output_tokens / 1000000).toFixed(1)}M</p>
            </div>
          </div>
        </animated.div>
      )}

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <CostByTypeChart data={costSummary?.cost_by_type || {}} />
        <MonthlyComparisonChart data={costSummary?.cost_by_day || []} />
      </div>

      {/* Cost Projections */}
      <CostProjections currentData={costSummary} />
    </div>
  )
}