import { useSpring, animated } from '@react-spring/web'
import { Bar } from 'react-chartjs-2'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions
} from 'chart.js'
import { formatCurrency } from '@/lib/utils'

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
)

interface CostByTypeChartProps {
  data: Record<string, number>
}

export default function CostByTypeChart({ data }: CostByTypeChartProps) {
  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'scale(0.95)' },
    to: { opacity: 1, transform: 'scale(1)' },
    delay: 400,
    config: { tension: 200, friction: 25 }
  })

  const sortedData = Object.entries(data).sort(([, a], [, b]) => b - a)

  const chartData = {
    labels: sortedData.map(([type]) => type.charAt(0).toUpperCase() + type.slice(1)),
    datasets: [
      {
        label: 'Cost by Review Type',
        data: sortedData.map(([, cost]) => cost),
        backgroundColor: [
          'rgba(168, 85, 247, 0.8)',
          'rgba(147, 51, 234, 0.8)',
          'rgba(196, 181, 253, 0.8)',
          'rgba(221, 214, 254, 0.8)',
          'rgba(237, 233, 254, 0.8)'
        ],
        borderColor: [
          'rgb(168, 85, 247)',
          'rgb(147, 51, 234)',
          'rgb(196, 181, 253)',
          'rgb(221, 214, 254)',
          'rgb(237, 233, 254)'
        ],
        borderWidth: 2,
        borderRadius: 8,
      }
    ]
  }

  const options: ChartOptions<'bar'> = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        padding: 12,
        cornerRadius: 8,
        callbacks: {
          label: (context) => {
            const total = context.dataset.data.reduce((a, b) => (a as number) + (b as number), 0) as number
            const percentage = ((context.parsed.y / total) * 100).toFixed(1)
            return `${formatCurrency(context.parsed.y)} (${percentage}%)`
          }
        }
      }
    },
    scales: {
      x: {
        grid: {
          display: false
        },
        ticks: {
          font: {
            size: 12
          }
        }
      },
      y: {
        beginAtZero: true,
        grid: {
          color: 'rgba(0, 0, 0, 0.05)'
        },
        ticks: {
          callback: (value) => formatCurrency(value as number),
          font: {
            size: 12
          }
        }
      }
    }
  }

  return (
    <animated.div style={containerSpring} className="glass rounded-xl p-6">
      <div className="mb-4">
        <h3 className="text-lg font-semibold">Cost by Review Type</h3>
        <p className="text-sm text-gray-600 dark:text-gray-400">Breakdown of costs by review type</p>
      </div>
      <div className="h-80">
        <Bar data={chartData} options={options} />
      </div>
    </animated.div>
  )
}