import { useState } from 'react'
import { useQuery } from '@tanstack/react-query'
import { useSpring, animated, useTrail } from '@react-spring/web'
import { 
  GitBranch, 
  Search, 
  Filter,
  TrendingUp,
  TrendingDown,
  Clock,
  DollarSign
} from 'lucide-react'
import { api, mockData } from '@/lib/api'
import { formatCurrency, formatDate, cn } from '@/lib/utils'

export default function Repositories() {
  const [searchTerm, setSearchTerm] = useState('')
  const [sortBy, setSortBy] = useState<'name' | 'cost' | 'reviews'>('cost')

  const { data: repositories = [], isLoading } = useQuery({
    queryKey: ['repositories'],
    queryFn: async () => {
      if (import.meta.env.DEV) return mockData.repositories
      return api.getRepositories()
    }
  })

  const headerSpring = useSpring({
    from: { opacity: 0, transform: 'translateY(-20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    config: { tension: 200, friction: 25 }
  })

  const searchSpring = useSpring({
    from: { opacity: 0, transform: 'scale(0.95)' },
    to: { opacity: 1, transform: 'scale(1)' },
    delay: 100,
    config: { tension: 200, friction: 25 }
  })

  // Filter and sort repositories
  const filteredRepos = repositories
    .filter(repo => repo.name.toLowerCase().includes(searchTerm.toLowerCase()))
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name)
        case 'cost':
          return b.total_cost - a.total_cost
        case 'reviews':
          return b.total_reviews - a.total_reviews
        default:
          return 0
      }
    })

  const trail = useTrail(filteredRepos.length, {
    from: { opacity: 0, transform: 'translateY(20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    config: { tension: 200, friction: 20 }
  })

  const getReviewTypeDistribution = (types: Record<string, number>) => {
    const total = Object.values(types).reduce((sum, count) => sum + count, 0)
    return Object.entries(types).map(([type, count]) => ({
      type,
      count,
      percentage: (count / total) * 100
    }))
  }

  return (
    <div className="p-8 space-y-8">
      {/* Header */}
      <animated.div style={headerSpring}>
        <h1 className="text-3xl font-bold gradient-text">Repositories</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-1">
          Manage and monitor Claude reviews across your repositories
        </p>
      </animated.div>

      {/* Search and Filters */}
      <animated.div style={searchSpring} className="glass rounded-xl p-6">
        <div className="flex flex-col md:flex-row gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-gray-400" />
            <input
              type="text"
              placeholder="Search repositories..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500 focus:border-transparent transition-all"
            />
          </div>
          
          <div className="flex items-center gap-2">
            <Filter className="h-5 w-5 text-gray-500" />
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as any)}
              className="px-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500 focus:border-transparent"
            >
              <option value="cost">Sort by Cost</option>
              <option value="reviews">Sort by Reviews</option>
              <option value="name">Sort by Name</option>
            </select>
          </div>
        </div>
      </animated.div>

      {/* Repository List */}
      <div className="grid gap-6">
        {isLoading ? (
          // Loading skeletons
          [...Array(3)].map((_, i) => (
            <div key={i} className="glass rounded-xl p-6">
              <div className="skeleton h-6 w-1/3 mb-4 rounded" />
              <div className="skeleton h-4 w-2/3 mb-2 rounded" />
              <div className="skeleton h-4 w-1/2 rounded" />
            </div>
          ))
        ) : (
          trail.map((style, index) => {
            const repo = filteredRepos[index]
            if (!repo) return null

            const typeDistribution = getReviewTypeDistribution(repo.review_types)
            const costTrend = Math.random() > 0.5 // Mock trend data

            return (
              <animated.div
                key={repo.name}
                style={style}
                className="glass rounded-xl p-6 card-hover"
              >
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-xl font-semibold flex items-center gap-2">
                      <GitBranch className="h-5 w-5 text-claude-500" />
                      {repo.name}
                    </h3>
                    <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
                      Last reviewed {formatDate(repo.last_review)}
                    </p>
                  </div>
                  
                  <div className="text-right">
                    <div className="flex items-center gap-2">
                      <span className="text-2xl font-bold">{formatCurrency(repo.total_cost)}</span>
                      {costTrend ? (
                        <TrendingUp className="h-5 w-5 text-cost-high" />
                      ) : (
                        <TrendingDown className="h-5 w-5 text-cost-low" />
                      )}
                    </div>
                    <p className="text-sm text-gray-600 dark:text-gray-400">
                      {repo.total_reviews} reviews
                    </p>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
                  <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-3">
                    <div className="flex items-center gap-2 text-sm text-gray-600 dark:text-gray-400 mb-1">
                      <DollarSign className="h-4 w-4" />
                      Avg Cost/Review
                    </div>
                    <p className="text-lg font-semibold">{formatCurrency(repo.average_cost)}</p>
                  </div>
                  
                  <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-3">
                    <div className="flex items-center gap-2 text-sm text-gray-600 dark:text-gray-400 mb-1">
                      <Clock className="h-4 w-4" />
                      Reviews/Week
                    </div>
                    <p className="text-lg font-semibold">{Math.round(repo.total_reviews / 4)}</p>
                  </div>
                  
                  <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-3">
                    <div className="flex items-center gap-2 text-sm text-gray-600 dark:text-gray-400 mb-1">
                      <TrendingUp className="h-4 w-4" />
                      Cost Trend
                    </div>
                    <p className={cn(
                      "text-lg font-semibold",
                      costTrend ? "text-cost-high" : "text-cost-low"
                    )}>
                      {costTrend ? '+' : '-'}{(Math.random() * 20 + 5).toFixed(1)}%
                    </p>
                  </div>
                </div>

                {/* Review Type Distribution */}
                <div className="space-y-2">
                  <p className="text-sm font-medium text-gray-700 dark:text-gray-300">Review Types</p>
                  <div className="flex gap-2 flex-wrap">
                    {typeDistribution.map(({ type, count, percentage }) => (
                      <div
                        key={type}
                        className="flex items-center gap-2 px-3 py-1 bg-claude-100 dark:bg-claude-900/20 rounded-full text-sm"
                      >
                        <span className="font-medium">{type}</span>
                        <span className="text-claude-700 dark:text-claude-300">
                          {count} ({percentage.toFixed(0)}%)
                        </span>
                      </div>
                    ))}
                  </div>
                </div>

                {/* Actions */}
                <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
                  <div className="flex gap-3">
                    <button className="px-4 py-2 text-sm bg-claude-500 text-white rounded-lg hover:bg-claude-600 transition-colors">
                      View Details
                    </button>
                    <button className="px-4 py-2 text-sm bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg hover:border-claude-500 transition-colors">
                      Configure
                    </button>
                    <button className="px-4 py-2 text-sm bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg hover:border-claude-500 transition-colors">
                      Disable Reviews
                    </button>
                  </div>
                </div>
              </animated.div>
            )
          })
        )}
      </div>

      {filteredRepos.length === 0 && !isLoading && (
        <div className="text-center py-12">
          <p className="text-gray-500 dark:text-gray-400">
            {searchTerm ? `No repositories found matching "${searchTerm}"` : 'No repositories found'}
          </p>
        </div>
      )}
    </div>
  )
}