import { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { useSpring, animated } from '@react-spring/web'
import { 
  Save,
  AlertCircle,
  Bell,
  DollarSign,
  FileText,
  Check
} from 'lucide-react'
import { api } from '@/lib/api'
import { OrganizationSettings } from '@/types'

const defaultSettings: OrganizationSettings = {
  monthly_budget: 500,
  per_repo_daily: 10,
  per_pr_maximum: 5,
  default_review_type: 'standard',
  auto_incremental: true,
  skip_patterns: ['*.lock', '*.generated.*', 'dist/*', 'build/*'],
  alert_thresholds: {
    daily: 50,
    weekly: 200,
    monthly: 400
  }
}

export default function Settings() {
  const queryClient = useQueryClient()
  const [savedMessage, setSavedMessage] = useState(false)

  const { data: settings = defaultSettings } = useQuery({
    queryKey: ['settings'],
    queryFn: async () => {
      if (import.meta.env.DEV) return defaultSettings
      return api.getSettings()
    }
  })

  const [formData, setFormData] = useState<OrganizationSettings>(settings)

  const updateMutation = useMutation({
    mutationFn: (data: Partial<OrganizationSettings>) => api.updateSettings(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['settings'] })
      setSavedMessage(true)
      setTimeout(() => setSavedMessage(false), 3000)
    }
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    updateMutation.mutate(formData)
  }

  const headerSpring = useSpring({
    from: { opacity: 0, transform: 'translateY(-20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    config: { tension: 200, friction: 25 }
  })

  const sections = [
    {
      title: 'Budget Limits',
      icon: DollarSign,
      fields: (
        <>
          <div>
            <label className="block text-sm font-medium mb-2">Monthly Budget</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.monthly_budget}
                onChange={(e) => setFormData({ ...formData, monthly_budget: Number(e.target.value) })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
            <p className="text-xs text-gray-500 mt-1">Maximum monthly spend across all repositories</p>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Daily Limit per Repository</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.per_repo_daily}
                onChange={(e) => setFormData({ ...formData, per_repo_daily: Number(e.target.value) })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Maximum Cost per PR</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.per_pr_maximum}
                onChange={(e) => setFormData({ ...formData, per_pr_maximum: Number(e.target.value) })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
          </div>
        </>
      )
    },
    {
      title: 'Review Defaults',
      icon: FileText,
      fields: (
        <>
          <div>
            <label className="block text-sm font-medium mb-2">Default Review Type</label>
            <select
              value={formData.default_review_type}
              onChange={(e) => setFormData({ ...formData, default_review_type: e.target.value })}
              className="w-full px-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
            >
              <option value="quick">Quick</option>
              <option value="standard">Standard</option>
              <option value="deep">Deep</option>
              <option value="security">Security</option>
            </select>
          </div>
          
          <div>
            <label className="flex items-center gap-3 cursor-pointer">
              <input
                type="checkbox"
                checked={formData.auto_incremental}
                onChange={(e) => setFormData({ ...formData, auto_incremental: e.target.checked })}
                className="w-5 h-5 text-claude-600 rounded focus:ring-2 focus:ring-claude-500"
              />
              <span className="text-sm font-medium">
                Automatically use incremental reviews for PR updates
              </span>
            </label>
            <p className="text-xs text-gray-500 mt-1 ml-8">Saves 50-70% on review costs</p>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Skip Patterns</label>
            <textarea
              value={formData.skip_patterns.join('\n')}
              onChange={(e) => setFormData({ 
                ...formData, 
                skip_patterns: e.target.value.split('\n').filter(p => p.trim()) 
              })}
              rows={4}
              className="w-full px-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500 font-mono text-sm"
              placeholder="*.lock&#10;*.generated.*&#10;dist/*"
            />
            <p className="text-xs text-gray-500 mt-1">One pattern per line</p>
          </div>
        </>
      )
    },
    {
      title: 'Alert Thresholds',
      icon: Bell,
      fields: (
        <>
          <div>
            <label className="block text-sm font-medium mb-2">Daily Alert Threshold</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.alert_thresholds.daily}
                onChange={(e) => setFormData({ 
                  ...formData, 
                  alert_thresholds: { ...formData.alert_thresholds, daily: Number(e.target.value) }
                })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Weekly Alert Threshold</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.alert_thresholds.weekly}
                onChange={(e) => setFormData({ 
                  ...formData, 
                  alert_thresholds: { ...formData.alert_thresholds, weekly: Number(e.target.value) }
                })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Monthly Alert Threshold</label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="number"
                value={formData.alert_thresholds.monthly}
                onChange={(e) => setFormData({ 
                  ...formData, 
                  alert_thresholds: { ...formData.alert_thresholds, monthly: Number(e.target.value) }
                })}
                className="w-full pl-8 pr-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
              />
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-2">Notification Webhook (Optional)</label>
            <input
              type="url"
              value={formData.notification_webhook || ''}
              onChange={(e) => setFormData({ ...formData, notification_webhook: e.target.value })}
              placeholder="https://hooks.slack.com/..."
              className="w-full px-4 py-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-claude-500"
            />
            <p className="text-xs text-gray-500 mt-1">Slack or Discord webhook URL for cost alerts</p>
          </div>
        </>
      )
    }
  ]

  return (
    <div className="p-8 space-y-8">
      {/* Header */}
      <animated.div style={headerSpring}>
        <h1 className="text-3xl font-bold gradient-text">Settings</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-1">
          Configure organization-wide Claude review settings
        </p>
      </animated.div>

      {/* Settings Form */}
      <form onSubmit={handleSubmit} className="space-y-8">
        {sections.map((section, index) => (
          <animated.div
            key={section.title}
            style={useSpring({
              from: { opacity: 0, transform: 'translateY(20px)' },
              to: { opacity: 1, transform: 'translateY(0px)' },
              delay: index * 100,
              config: { tension: 200, friction: 25 }
            })}
            className="glass rounded-xl p-6"
          >
            <div className="flex items-center gap-3 mb-6">
              <div className="p-2 bg-claude-100 dark:bg-claude-900/20 rounded-lg">
                <section.icon className="h-5 w-5 text-claude-600" />
              </div>
              <h3 className="text-lg font-semibold">{section.title}</h3>
            </div>
            
            <div className="space-y-4">
              {section.fields}
            </div>
          </animated.div>
        ))}

        {/* Save Button */}
        <animated.div
          style={useSpring({
            from: { opacity: 0 },
            to: { opacity: 1 },
            delay: sections.length * 100
          })}
          className="flex items-center justify-between"
        >
          <div className="flex items-center gap-3">
            <AlertCircle className="h-5 w-5 text-amber-500" />
            <p className="text-sm text-gray-600 dark:text-gray-400">
              Changes apply to all repositories in the organization
            </p>
          </div>
          
          <div className="flex items-center gap-3">
            {savedMessage && (
              <animated.div
                style={useSpring({
                  from: { opacity: 0, scale: 0.8 },
                  to: { opacity: 1, scale: 1 }
                })}
                className="flex items-center gap-2 text-green-600"
              >
                <Check className="h-5 w-5" />
                <span className="text-sm font-medium">Settings saved!</span>
              </animated.div>
            )}
            
            <button
              type="submit"
              disabled={updateMutation.isPending}
              className="flex items-center gap-2 px-6 py-2 bg-claude-500 text-white rounded-lg hover:bg-claude-600 transition-colors disabled:opacity-50"
            >
              <Save className="h-4 w-4" />
              {updateMutation.isPending ? 'Saving...' : 'Save Settings'}
            </button>
          </div>
        </animated.div>
      </form>
    </div>
  )
}