import { useEffect, useState } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import { useSpring, animated } from '@react-spring/web'
import { Github, AlertCircle } from 'lucide-react'
import { api } from '@/lib/api'

export default function Login() {
  const navigate = useNavigate()
  const [searchParams] = useSearchParams()
  const [error, setError] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)

  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'scale(0.95)' },
    to: { opacity: 1, transform: 'scale(1)' },
    config: { tension: 200, friction: 25 }
  })

  useEffect(() => {
    // Check if already authenticated
    if (api.isAuthenticated()) {
      navigate('/')
      return
    }

    // Handle OAuth callback
    const code = searchParams.get('code')
    if (code) {
      handleOAuthCallback(code)
    }
  }, [searchParams, navigate])

  const handleOAuthCallback = async (code: string) => {
    setLoading(true)
    try {
      const response = await fetch(`/.netlify/functions/auth?code=${code}`)
      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.message || data.error || 'Authentication failed')
      }

      api.setToken(data.token)
      navigate('/')
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Authentication failed')
      setLoading(false)
    }
  }

  const handleGitHubLogin = () => {
    // Redirect to token-based login since OAuth app setup is complex
    navigate('/login')
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 flex items-center justify-center p-4">
      <animated.div style={containerSpring} className="w-full max-w-md">
        <div className="glass rounded-2xl p-8 space-y-6">
          <div className="text-center space-y-2">
            <h1 className="text-3xl font-bold gradient-text">Claude Review Dashboard</h1>
            <p className="text-gray-600 dark:text-gray-400">
              Sign in to access your cost analytics
            </p>
          </div>

          {error && (
            <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
              <div className="flex items-start gap-3">
                <AlertCircle className="h-5 w-5 text-red-600 dark:text-red-400 flex-shrink-0 mt-0.5" />
                <div>
                  <h3 className="font-medium text-red-800 dark:text-red-200">
                    Authentication Error
                  </h3>
                  <p className="text-sm text-red-600 dark:text-red-400 mt-1">
                    {error}
                  </p>
                </div>
              </div>
            </div>
          )}

          <div className="space-y-4">
            <button
              onClick={handleGitHubLogin}
              disabled={loading}
              className="w-full flex items-center justify-center gap-3 px-6 py-3 bg-gray-900 dark:bg-white text-white dark:text-gray-900 rounded-lg hover:bg-gray-800 dark:hover:bg-gray-100 transition-colors disabled:opacity-50"
            >
              <Github className="h-5 w-5" />
              {loading ? 'Authenticating...' : 'Sign in with GitHub'}
            </button>

            <p className="text-xs text-center text-gray-500 dark:text-gray-400">
              Only authorized users (Aaron, Tyler, and Patrick) can access this dashboard
            </p>
          </div>
        </div>

        <p className="text-center text-sm text-gray-500 dark:text-gray-400 mt-6">
          Need access? Contact your administrator
        </p>
      </animated.div>
    </div>
  )
}