import { useQuery } from '@tanstack/react-query'
import { useSpring, animated, useTrail } from '@react-spring/web'
import { GitPullRequest, Clock, ExternalLink } from 'lucide-react'
import { api, mockData } from '@/lib/api'
import { formatCurrency, formatDuration, formatDate, getCostColor, getCostBgColor } from '@/lib/utils'

export default function RecentReviews() {
  const { data: reviews = [], isLoading } = useQuery({
    queryKey: ['recentReviews'],
    queryFn: async () => {
      if (import.meta.env.DEV) return mockData.reviews
      return api.getRecentReviews(10)
    }
  })

  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'translateY(20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    delay: 400,
    config: { tension: 200, friction: 25 }
  })

  const trail = useTrail(reviews.length, {
    from: { opacity: 0, transform: 'translateX(-20px)' },
    to: { opacity: 1, transform: 'translateX(0px)' },
    config: { tension: 200, friction: 20 }
  })

  const getReviewTypeColor = (type: string) => {
    const colors: Record<string, string> = {
      quick: 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-300',
      standard: 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-300',
      deep: 'bg-purple-100 text-purple-800 dark:bg-purple-900/20 dark:text-purple-300',
      security: 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-300',
      incremental: 'bg-amber-100 text-amber-800 dark:bg-amber-900/20 dark:text-amber-300',
    }
    return colors[type] || colors.standard
  }

  if (isLoading) {
    return (
      <animated.div style={containerSpring} className="glass rounded-xl p-6">
        <h3 className="text-lg font-semibold mb-4">Recent Reviews</h3>
        <div className="space-y-3">
          {[1, 2, 3].map((i) => (
            <div key={i} className="p-4 bg-gray-50 dark:bg-gray-800/50 rounded-lg">
              <div className="skeleton h-4 w-3/4 mb-2 rounded" />
              <div className="skeleton h-3 w-1/2 rounded" />
            </div>
          ))}
        </div>
      </animated.div>
    )
  }

  return (
    <animated.div style={containerSpring} className="glass rounded-xl p-6">
      <div className="mb-4 flex items-center justify-between">
        <h3 className="text-lg font-semibold">Recent Reviews</h3>
        <button className="text-sm text-claude-600 hover:text-claude-700 font-medium">
          View All →
        </button>
      </div>

      <div className="space-y-3">
        {trail.map((style, index) => {
          const review = reviews[index]
          if (!review) return null

          return (
            <animated.div
              key={review.review_id}
              style={style}
              className="p-4 bg-gray-50 dark:bg-gray-800/50 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors cursor-pointer group"
            >
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <div className="flex items-center gap-3 mb-1">
                    <GitPullRequest className="h-4 w-4 text-gray-500" />
                    <span className="font-medium text-sm">
                      {review.repository} #{review.pr_number}
                    </span>
                    <span className={`text-xs px-2 py-0.5 rounded-full ${getReviewTypeColor(review.review_type)}`}>
                      {review.review_type}
                    </span>
                  </div>
                  
                  <div className="flex items-center gap-4 text-xs text-gray-600 dark:text-gray-400">
                    <span className="flex items-center gap-1">
                      <Clock className="h-3 w-3" />
                      {formatDuration(review.duration_seconds)}
                    </span>
                    <span>{formatDate(review.timestamp)}</span>
                    <span className="flex items-center gap-1">
                      {formatNumber(review.input_tokens)} → {formatNumber(review.output_tokens)} tokens
                    </span>
                  </div>
                </div>

                <div className="flex items-center gap-3">
                  <div className={`px-3 py-1 rounded-lg ${getCostBgColor(review.total_cost)}`}>
                    <span className={`font-semibold ${getCostColor(review.total_cost)}`}>
                      {formatCurrency(review.total_cost)}
                    </span>
                  </div>
                  <ExternalLink className="h-4 w-4 text-gray-400 group-hover:text-gray-600 transition-colors" />
                </div>
              </div>
            </animated.div>
          )
        })}
      </div>
    </animated.div>
  )
}

function formatNumber(num: number): string {
  if (num >= 1000000) return `${(num / 1000000).toFixed(1)}M`
  if (num >= 1000) return `${(num / 1000).toFixed(1)}K`
  return num.toString()
}