import { useSpring, animated, useTrail } from '@react-spring/web'
import { TrendingUp, Calendar, Target, AlertTriangle } from 'lucide-react'
import { formatCurrency } from '@/lib/utils'
import { CostSummary } from '@/types'

interface CostProjectionsProps {
  currentData?: CostSummary
}

export default function CostProjections({ currentData }: CostProjectionsProps) {
  const containerSpring = useSpring({
    from: { opacity: 0, transform: 'translateY(20px)' },
    to: { opacity: 1, transform: 'translateY(0px)' },
    delay: 600,
    config: { tension: 200, friction: 25 }
  })

  if (!currentData) return null

  // Calculate projections
  const dailyAverage = currentData.total_cost / currentData.cost_by_day.length
  const monthlyProjection = dailyAverage * 30
  const yearlyProjection = monthlyProjection * 12
  const quarterlyProjection = monthlyProjection * 3

  // Calculate growth rate
  const firstWeekAvg = currentData.cost_by_day.slice(0, 7).reduce((sum, d) => sum + d.cost, 0) / 7
  const lastWeekAvg = currentData.cost_by_day.slice(-7).reduce((sum, d) => sum + d.cost, 0) / 7
  const growthRate = ((lastWeekAvg - firstWeekAvg) / firstWeekAvg) * 100

  const projections = [
    {
      label: 'Monthly Projection',
      value: formatCurrency(monthlyProjection),
      icon: Calendar,
      description: 'Based on current daily average',
      color: monthlyProjection > 500 ? 'text-cost-high' : 'text-cost-low',
      bgColor: monthlyProjection > 500 ? 'bg-cost-high/10' : 'bg-cost-low/10'
    },
    {
      label: 'Quarterly Estimate',
      value: formatCurrency(quarterlyProjection),
      icon: Target,
      description: '3-month projection',
      color: 'text-claude-600',
      bgColor: 'bg-claude-100 dark:bg-claude-900/20'
    },
    {
      label: 'Annual Forecast',
      value: formatCurrency(yearlyProjection),
      icon: TrendingUp,
      description: '12-month projection',
      color: 'text-purple-600',
      bgColor: 'bg-purple-100 dark:bg-purple-900/20'
    },
    {
      label: 'Growth Trend',
      value: `${growthRate > 0 ? '+' : ''}${growthRate.toFixed(1)}%`,
      icon: growthRate > 10 ? AlertTriangle : TrendingUp,
      description: 'Week-over-week change',
      color: growthRate > 10 ? 'text-cost-high' : growthRate < 0 ? 'text-cost-low' : 'text-amber-600',
      bgColor: growthRate > 10 ? 'bg-cost-high/10' : growthRate < 0 ? 'bg-cost-low/10' : 'bg-amber-100 dark:bg-amber-900/20'
    }
  ]

  const trail = useTrail(projections.length, {
    from: { opacity: 0, transform: 'scale(0.9)' },
    to: { opacity: 1, transform: 'scale(1)' },
    config: { tension: 200, friction: 25 }
  })

  return (
    <animated.div style={containerSpring} className="glass rounded-xl p-6">
      <h3 className="text-lg font-semibold mb-6">Cost Projections</h3>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        {trail.map((style, index) => {
          const projection = projections[index]
          return (
            <animated.div
              key={projection.label}
              style={style}
              className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4"
            >
              <div className={`inline-flex p-2 rounded-lg ${projection.bgColor} mb-3`}>
                <projection.icon className={`h-5 w-5 ${projection.color}`} />
              </div>
              <h4 className="text-sm font-medium text-gray-600 dark:text-gray-400">
                {projection.label}
              </h4>
              <p className={`text-2xl font-bold mt-1 ${projection.color}`}>
                {projection.value}
              </p>
              <p className="text-xs text-gray-500 dark:text-gray-400 mt-2">
                {projection.description}
              </p>
            </animated.div>
          )
        })}
      </div>

      {/* Recommendations */}
      <div className="mt-6 p-4 bg-amber-50 dark:bg-amber-900/10 rounded-lg border border-amber-200 dark:border-amber-800">
        <div className="flex items-start gap-3">
          <AlertTriangle className="h-5 w-5 text-amber-600 flex-shrink-0 mt-0.5" />
          <div>
            <h4 className="font-medium text-amber-900 dark:text-amber-100">
              Cost Optimization Recommendations
            </h4>
            <ul className="mt-2 space-y-1 text-sm text-amber-800 dark:text-amber-200">
              {monthlyProjection > 500 && (
                <li>• Monthly costs exceeding budget - consider enabling auto-incremental reviews</li>
              )}
              {growthRate > 10 && (
                <li>• High growth rate detected - review your default review settings</li>
              )}
              <li>• Use `/claude-review quick` for simple PRs to reduce costs by 60-80%</li>
              <li>• Enable file pattern filtering to skip generated files</li>
            </ul>
          </div>
        </div>
      </div>
    </animated.div>
  )
}